import { DetailedHTMLProps, HTMLAttributes, useEffect, useRef, useState } from "react";
import Icon from "./Icon";

type Props = DetailedHTMLProps<HTMLAttributes<HTMLParagraphElement>, HTMLParagraphElement> & { text: string }

function AriaLabel(props: Props) {
	const [overflowing, setOverflowing] = useState(false);
	const element = useRef<HTMLParagraphElement>(null);

	useEffect(() => {
		if (element.current) {
			const rect = element.current.getBoundingClientRect();
			setOverflowing(rect.top < 0);
		}
	}, []);

	return (<div className="arialabel">
		{props.children}<Icon icon="open_in_new"/>
		<p {...props} ref={element} className={`${props.className} text ${overflowing ? "revert" : ""}`}>{
			props.text
			.split(/(\n)|(\[([A-Za-z0-9]*)\]\((https?:\/\/[A-Za-z0-9\.]*[a-z0-9]{2,}\/?[A-Za-z0-9\/?]*)\))|[icon|ICON]:([A-Za-z0-9]*)/g).map((text) => {
				const regex = {
					a: /^\[([A-Za-z0-9]*)\]\((https?:\/\/[A-Za-z0-9\.]*[a-z0-9]{2,}\/?[A-Za-z0-9\/?]*)\)$/,
					breakLine: /(\n)/,
					icon: /icon:([A-Za-z0-9]*)/
				}
				if (regex.a.test(text)) {
					return (<a href={text.split(regex.a)[2]} target="_blank">{text.split(regex.a)[1]}</a>);
				}
				if (regex.icon.test(text)) {
					return (<Icon icon={text.split(regex.icon)[1] as any || "report"}/>);
				}
				if (regex.breakLine.test(text)) {
					return (<>
						{text}<br/>
					</>);
				}
				return <>{text}</>
			})
		}</p>
	</div>)
}

export default AriaLabel;